.Title {
  font-size: 2em;
  font-family: 'Kanit', sans-serif;
  margin-bottom: 40px;  
  text-shadow: white 0 0 0px;
}

.Title .Comment {
  font-size: .75em;
  font-family: inherit;
  text-shadow: none;
  color: #595959;
}

.list-group {
  padding: 0 0 0 0;
  background-color: inherit;
}

.list-group .list-group-item {
  background-color: inherit;
  font-family: 'Lato', sans-serif;
  margin-bottom: 10px;
  border: 0;
  padding: inherit;
  font-size: 1.1em;
}

body {
  background-color: #212121;
  padding: 8em 0 0 4em;
  color: white;
}

a {
  color: white;
  text-shadow: black 0 0 1px; 
  text-decoration: none;
}

a:hover {
  font-weight: bold;
  color: white;
  text-shadow: black 0 0 1px;
  text-decoration: none;
}

